const world = 'World';
export function hello(word = world) {
    return `Hello ${word}!`;
}
export const test = () => {
    const obj = { name: 'Tommy', age: '27' };
    console.log({ id: '1', ...obj });
};
class TestClass {
    constructor() {
        this.tryThis = (thing = 'THING') => {
            return `Tried ${thing}`;
        };
        this.instanceProp = 'Bork';
    }
}
TestClass.staticProp = 'static';
TestClass.fun = function () {
    return 'Fun!';
};
console.log('Here!');
test();
const cl = new TestClass();
console.log(cl.tryThis(), cl.instanceProp, TestClass.staticProp, TestClass.fun());
